.mobile {
  background-color: #191919;
  position: fixed;
  top: 0;
  bottom: 0;
  width: 80%;
  padding: 1em;
  transition: transform .25s;
  z-index: 1000;
  opacity: 0;
}

.mobile.left {
  right: auto;
  left: 0;
  transform: translateX(-100%);
}

.mobile.right {
  left: auto;
  right: 0;
  transform: translateX(100%);
}

.mobile.open {
  transform: translateX(0);
  transition: transform .25s;
}

.mobile.init {
  display: block;
  opacity: 1;
}

.mobile nav.utility ul li a {
  padding: .75em 0;
}

.mobile ul {
  padding-left: 0;
}

.mobile ul li {
  text-align: left;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}

.mobile ul li a {
  color: #fff;
  display: block;
  padding: 1em 0;
}

.mobile ul li i.fa {
  display: inline-block;
}

@media only screen and (min-width: 1024px) {
  .mobile.open.left {
    transform: translateX(-100%);
  }
  .mobile.open.right {
    transform: translateX(100%);
  }
}

