/* = Media Queries = */
@media only screen and (min-width: 669px) {
  /* = Layout = */
  aside.primary {
    float: right;
    margin-bottom: 0;
    width: 20%;
  }
  aside.primary nav ul {
    text-align: left;
  }
  aside.primary ~ main {
    float: left;
    padding-right: 2rem;
    width: 80%;
  }
}

@media only screen and (min-width: 1024px) {
  /* = Layout = */
  .wrapper {
    overflow: auto;
  }
  nav ul {
    text-align: left;
  }
  nav.primary {
    margin-bottom: 0;
  }
  nav.primary > ul > li {
    font-size: 1.3rem;
    margin-left: 1rem;
    position: relative;
  }
  nav.primary > ul > li:first-child {
    margin-left: 0;
  }
  nav.primary ul {
    text-align: left;
  }
  nav.primary ul li li {
    display: block;
    float: none;
  }
  .utilities nav.utility {
    margin-bottom: 0.4rem;
  }
  .utilities nav.utility ul {
    text-align: right;
  }
  .desktop {
    display: flex;
  }
  button.menu-toggle {
    display: none;
  }
  aside.primary {
    width: 25%;
  }
  aside.primary ~ main {
    width: 75%;
  }
  /* = Typography = */
  h1 {
    font-size: 2.6rem;
  }
  h2 {
    font-size: 2.1rem;
  }
  h3 {
    font-size: 1.6rem;
  }
  h4 {
    font-size: 1.25rem;
  }
  h5 {
    font-size: 1.15rem;
  }
}

/* = Print Styles = */
@media print {
  * {
    background-color: transparent !important;
    box-shadow: none !important;
    color: #000 !important;
    /* Black prints faster: h5bp.com/s */
    text-shadow: none !important;
  }
  a {
    text-decoration: underline;
  }
  pre,
  blockquote,
  tr,
  img,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  @page {
    margin: 0.5cm;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
}
